.actors-card {
    
    &:hover {
        .actors {
            transform: scale(1.1);
            transition: all ease 0.5s;
        }

        
    }
    .actor-name {
        color: white;
        margin-top: 10px;
    }
    .actors {
        width: 180px;
        height: 180px;
        // margin: 10px;
        // overflow: hidden;
        .actor-img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
        }
        
    }
}