.header {
background: #16151A;
padding: 35px 0;
box-shadow: 0px 1px 9px #333333;
    .one-spane {
        padding: 3px;
        background-color: white;
        padding: 0 5px;
        margin: 0 3px;
        color: black;
        transition: all ease 0.6s;
    }

    .two-spane {
        padding: 3px;
        background-color: orange;
        padding: 0 5px;
        margin: 0 3px;
        color: white;
        transition: all ease 0.9s;
    }

    .header-left  {
        &:hover {

        
        .two-spane {
            background-color: white;
            color: black;
        }

        .one-spane {
            background-color: orange;
            color: white;
        }
        .q {
            transition: all ease 0.1s;
        }
        .w {
            transition: all ease 0.2s;
        }
        .e {
            transition: all ease 0.3s;
        }
        .r {
            transition: all ease 0.4s;
        }
        .t {
            transition: all ease 0.5s;
        }
        .y {
            transition: all ease 0.6s;
        }
        .u {
            transition: all ease 0.7s;
        }
        .i {
            transition: all ease 0.8s;
        }
        .o {
            transition: all ease 0.9s;
        }
        .p {
            transition: all ease 1s;
        }
    }
    }

    .modal-input {
        position: absolute;
        // width: 0;
        // height: 0;
        z-index: -1;
        position: fixed;
        transition: all ease 0.2s;

        &.activ {
        z-index: 5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.7);
        }

        
    }

    

    .header-inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin: 0 auto;
        padding-left: 15px;
        padding-right: 15px;
        max-width: 1140px;
        box-sizing: border-box;
        position: relative;

        

        .input-div {

            .input {
                // width: 100%;
                position: absolute;
                left: 0;
                top: -10px;
                right: 0;
                margin: auto;
                width: 90%;
                padding: 10px;
                background: #333333;
                color: white;
                font-size: 18px;
                border: none;
                border-radius: 5px;
                transform: translateY(-1000px);
                outline: none;
                transition: all ease 0.5s;
                z-index: 6;
                &::placeholder {
                    color: white;
                }
                &.activ {
                    transform: translateY(0);
                    z-index: 6;
                    position: fixed;
                    max-width: 70%;
                    top: 20px;
                    border: 1px solid orange;
                }
            }
        }

        .search-icon {
            width: 30px;
            height: 30px;
        }

        .header-right {
            // position: relative;

            

            .btn {
                padding: 5px;
                color: black;
                cursor: pointer;
                
            }

            

            
        }

        .header-link{
            padding: 10px;
            color: white;
            text-decoration: none;
            padding: 15px 15px;
            margin: 0 2px;
            border-top: 1px solid transparent;

            width: 0;
            height: 0;
            // transition: all ease 0.5s;

            
            &:active {
                
                background: orange;
            }

            &.activ {
                
                color: orange;
                border-bottom: 1px solid orange;
            }
            
            &:hover {
                border-top: 1px solid orange;
                border-bottom: 1px solid orange;
            }

        }

        .header-logotip {
            display: flex;
            align-items: center;

            .logotip {
                color: red;
                text-decoration: none;

                .logo {
                    color: white;
                    text-decoration: none;
                }
            }
        }
    }

    .search-result {
        &.activ {
            display: block;
        }
        position: fixed;
        width: 70%;
        top: 73px;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 10;
        border-radius: 50px;
        display: none;

        .allResults {
            padding: 10px;
            background: orange;
            color: black;
            width: 98.5%;
            display: inline-block;
            border-radius: 3px;
        }

        .search-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 5px;
            box-sizing: border-box;
            border-bottom: 1px solid black;
            background: #2d2c32;
            transition: all ease 0.5s;
            border-radius: 4px;
            &:hover {
                background: #26252B;
            }

            .search-img {
                width: 25px;
                height: 25px;
                display: block;
                &:last-child {
                    margin-top: 5px;
                }
            }

            .left-right {
                .film-date {
                    color: white;
                    margin-left: 10px;
                    display: block;
                    margin-top: 10px;
                }
                .film-name {
                    color: white;
                    margin-left: 10px;
                    margin-top: 10px;
                }
            }

            .search-left {
                display: flex;

                .film-photo {
                    width: 80px;
                    height: 80px;
                }
            }
        }
        
    }
    

    
}