body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    background: linear-gradient( #16151A, #16151A);
    
    
    

}

h1, h2, h3, h4, h5, h6, p, a {
    padding: 0;
    margin: 0;
    text-decoration: none;
}

.container {
    width: 100%;
    max-width: 1140px;
    padding-left: 15px;
    padding-right: 15px;
    margin: auto;
    box-sizing: border-box;
}

.slick-dots {
    display: none !important;
}

.appBlock {
    min-height: 81vh;
}

.slick-prev {
    position: absolute !important;
    top: -25px !important;
    right: 0 !important;
    transform: translateX(62rem) !important;
    width: 40px !important;
    height: 40px !important;
    background-color: orange !important;
    border-radius: 50%;

    &:hover {
        background-color: orangered !important;
    }

    &::after {
        position: absolute;
        content: "";
        width: 20px;
        height: 20px;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        background-image: url('./images/icon/130884.png');

        background-repeat: no-repeat;
        background-size: cover;
        background-position: center ;
        transform: rotate(180deg);
    }

    &::before {
        display: none !important;
    }
    
}

.slick-next {
    position: absolute !important;
    top: -25px !important;
    // right: 0 !important;
    transform: translateX(-58px) !important;
    background-color: orange !important;
    border-radius: 50%;
    width: 40px !important;
    height: 40px !important;

    &:hover {
        background-color: orangered !important;
    }

    &::after {
        position: absolute;
        content: "";
        width: 20px;
        height: 20px;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        background-image: url('./images/icon/130884.png');

        background-repeat: no-repeat;
        background-size: cover;
        background-position: center ;
    }

    &::before {
        display: none !important;
    }
}

.slick-track {
    margin-top: 30px !important;
}